import React, { useEffect, useRef, useState } from 'react';
import {
    Grid,
    Typography,
    FormControl,
    RadioGroup,
    FormLabel,
    FormControlLabel,
    Radio,
    Box,
    InputLabel,
    Select,
    List,
    ListItem,
    ListItemText,
    Fade,
    Collapse,
    Slide,
    MenuItem,
} from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';

import SealMaterials, { SealMaterial } from 'constants/SealMaterials';
import { ReactComponent as PdfIcon } from 'images/pdficon.svg';

const useStyles = makeStyles(theme => ({
    divMain: {
        paddingTop: theme.spacing(2.5),
        paddingBottom: theme.spacing(5.5),
        textAlign: 'center',
        alignContent: 'center',
    },

    boxLeft: {
        textAlign: 'left',
        width: 'fit-content',
        margin: 'auto'
    },

    boxRight: {
        margin: 'auto'
    },

    radio: {
        borderRadius: 0,
        boxShadow: 'none',
        backgroundColor: 'transparent',
    },

    divLeft: {
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
    },

    title: {
        margin: theme.spacing(2, 0, 0),
        color: theme.palette.primary.main,
    },

    select: {
        marginLeft: '10%',
        width: theme.spacing(40),
    },

    points: {
        borderColor: theme.palette.primary.main,
        borderRadius: '10%',
        border: '3px solid',
        color: theme.palette.primary.main,
        textAlign: 'left',
        width: 'fit-content',
        padding: theme.spacing(1, 3, 1, 2),
        margin: 'auto',
    },

    image: {
        width: '45%',
    }

}));

const MaterialSelection: React.FC = () => {

    const classes = useStyles();
    const [rawMaterial, setRawMaterial] = useState(-1);
    const [materialSelection, setMaterialSelection] = useState(-1);

    const rawMaterials = Object.keys(SealMaterials);
    const materials = rawMaterial === -1 ? [] : SealMaterials[rawMaterials[rawMaterial]];
    const rawMaterialName: string | null = rawMaterial === -1 ? null : rawMaterials[rawMaterial];
    const material: SealMaterial | null = materialSelection === -1 ? null : materials[materialSelection];

    return (
        <div className={classes.divMain}>
            <Grid container justify='center' alignContent='center'>
                <Grid item xs={6} >
                    <Box className={classes.boxLeft}>
                        <div>
                            <Typography color='primary' component='div' variant='h2' gutterBottom>
                                {'MEASUREMENTS AND MATERIAL​'}
                            </Typography>
                        </div>

                        <div className={classes.divLeft}>
                            <div>
                                <FormControl component="fieldset">
                                    <RadioGroup row name="unitid" defaultValue="inch">
                                        <FormControlLabel
                                            value="inch"
                                            control={<Radio className={classes.radio} size="small" color="primary" />}
                                            label="Inch"
                                        />
                                        <FormControlLabel
                                            value="mm"
                                            control={<Radio className={classes.radio} size="small" color="primary" />}
                                            label="Metric"
                                        />
                                    </RadioGroup>
                                </FormControl>
                            </div>

                            <div>
                                <SelectList title='RAW MATERIAL' options={rawMaterials} index={rawMaterial} onClick={(e, i) => {
                                    setRawMaterial(i);
                                    if (i != rawMaterial) {
                                        setMaterialSelection(-1);
                                    }
                                }} />
                            </div>

                            <div>
                                <SelectList title='MATERIAL SELECTION' options={materials.map(e => e.name)} index={materialSelection} onClick={(e, i) => setMaterialSelection(i)} />
                            </div>
                        </div>

                    </Box>
                </Grid>
                <Grid item xs={6}>
                    {rawMaterialName && material ? (<MaterialView rawMaterial={rawMaterialName!} material={material!} />) : (<></>)}
                </Grid>
            </Grid>
        </div>
    );
};


const MaterialView = ({ rawMaterial, material }: { rawMaterial: string, material: SealMaterial }) => {

    const classes = useStyles();
    const [updates, setUpdates] = useState(0);

    useEffect(() => {
        setUpdates(updates + 1);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [rawMaterial, material.name]);

    return (
        <Slide key={updates} direction="up" in={true} mountOnEnter unmountOnExit>
            <Box className={classes.boxRight}>
                <Typography color='primary' component='div' variant='h2' gutterBottom>
                    {material.name}
                </Typography>
                <div>
                    <img
                        className={classes.image}
                        alt={material.name}
                        src={`/seal/images/Materials/${rawMaterial}/${material.name}/${material.name}.jpg`}
                    />
                    <div>
                        <a target="_blank" rel="noreferrer" href={`/seal/images/Materials/${rawMaterial}/${material.name}/ds_dmh${material.name.toLowerCase().replace(/\s/g, '')}.pdf`}>
                            <PdfIcon />
                            <Typography component='span' variant='overline'>
                                FACT SHEET
                            </Typography>
                        </a>
                    </div>
                    <div className={classes.points} >
                        <ul>
                            {material.facts.map((e, i) => (<Fade in timeout={500} style={{ transitionDelay: `${i * 200}ms` }} key={e}><li>{e}</li></Fade>))}
                        </ul>
                    </div>
                </div>
            </Box>
        </Slide>
    );
}

const SelectList = ({ title, options, index, onClick }: { title: string, options: string[], index: number, onClick: (e: any, i: number) => any }) => {
    const classes = useStyles();
    const theme = useTheme();
    return (
        <div>
            <Typography variant="h5" className={classes.title}>
                {title}
            </Typography>
            <FormControl variant="outlined" size="small" className={classes.select}>
                <Select
                    value={index}
                    onChange={(e) => onClick(e, e.target.value as number)}
                >
                    {options.length ? <MenuItem value={-1} disabled>Please select</MenuItem> : <MenuItem value={-1}>No materials available</MenuItem>}
                    {options.length ? options.map((e, i) => (<MenuItem key={e} value={i}>{e}</MenuItem>)) : ([])}
                </Select>
            </FormControl>
        </div>
    );
}

export default MaterialSelection;